.switcher {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  user-select: none;
}

.switcher input {
  display: none;
}

.switcher input:checked ~ .switcher__control {
  background: #2e99db;
}

.switcher input:checked ~ .switcher__control:before {
  box-shadow: -1px 2px 2px rgba(0, 0, 0, 0.2);
  transform: translate3d(10px, 0, 0);
}

.switcher input:checked ~ .switcher__status [data-switcher="enabled"] {
  display: block;
}

.switcher input:checked ~ .switcher__status [data-switcher="disabled"] {
  display: none;
}

.switcher input:disabled ~ .switcher__label, .switcher input:disabled ~ .switcher__control, .switcher input:disabled ~ .switcher__status {
  cursor: not-allowed;
}

.switcher__label, .switcher__control, .switcher__status {
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
}

