  margin-right: 10px;
  font-weight: 500;
}

.switcher__status {
  margin-left: 10px;
  font-weight: 400;
}

.switcher__status [data-switcher="enabled"] {
  display: none;
}

.switcher__control {
  position: relative;
  border-radius: 15px;
  width: 50px;
  height: 30px;
  background: #eee;
  transition: background .2s, box-shadow .1s;
}

.switcher__control:active {
  box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.switcher__control:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -0.5em 0 0 -0.5em;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  background: #fff;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  font-size: 24px;
  transform: translate3d(-10px, 0, 0);
  transition: transform 0.2s,box-shadow 0.2s;
}

